#home {
    background-image: url(../../backgrounds/big_purp1.png), url(../../backgrounds/home.png);
    background-size: cover, contain;
    background-position: right 50% bottom 60vh, right 50% top 40vh;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    position: relative;
}

.home-title {
    position: absolute;
    top: 0;
    left: 0;
    text-align: left;
    z-index: 2;
}

h1 {
    color: rgba(255, 255, 255, 0.2);
    font-family: 'Didot', serif;
    font-size: 3.3rem;
    font-weight: bold;
}

.home-links {
    color: rgba(255, 255, 255, 0.9);
    padding-top: 3.3rem;
    position: absolute;
    top: 6.5rem;
    text-align: left;
}

.home-msg-container {
    overflow: hidden;
    padding-top: 3.3rem;
    position: absolute;
    top: 0;
    text-align: left;
}

.home-msg {
    animation: 1s ease-out slidein;
    color: rgba(146, 170, 67, 1);
    font-size: 1rem;
    letter-spacing: 4px;
    padding-left: 6px;
    z-index: 2;
}

.out-now {
    margin-top: 2.8rem;
}

.buy-listen {
    border-bottom: 1px solid rgba(255, 255, 255, 0.9);
    margin-top: 20px;
    padding-left: 10px;
    position: relative;
    width: fit-content;
    text-align: right;
    z-index: 1;
}

.patreon-link p {
    color: rgb(46, 12, 70);
    text-shadow: 0 0 30px rgba(146, 170, 67, 1), 1px 1px 20px rgba(146, 170, 67, 1), -1px -1px 20px rgba(146, 170, 67, 1), 0 10px 10px rgba(146, 170, 67, 1), 0 -10px 10px rgba(146, 170, 67, 1), 10px 0 10px rgba(146, 170, 67, 1), -10px 0 10px rgba(146, 170, 67, 1), 0 -5px 5px  rgba(146, 170, 67, 1), 0 5px 5px  rgba(146, 170, 67, 1);
}

.patreon-link-big {
    display: none;
}

.buy-listen p {
    color: rgba(255, 255, 255, 0.8);
}

.cover {
    text-align: center;
    width: 100%;
}

.album-cover {
    margin: 0 auto;
    width: 90%;
}



@keyframes slidein {
    from { 
        transform: translateX(-100%);
    }
    to { 
        transform: translateX(0);
    }
}

@media screen and (min-width: 560px)
              and (max-width: 830px)
              and (orientation: landscape) {
                  #home {
                      background-position: 50% 51%;
                  }
                  .album-cover {
                      width: 60%;
                  }
                  .patreon-link {
                      bottom: 7%;
                  }
              }


@media screen and (min-width: 600px)
              and (min-height: 450px) {
    #home {
        background-size: cover, cover;
        background-position: right 50% bottom 55vh, 50% 30%;
    }
    h1 {
        font-size: 3.5rem;
    }
    /* .buy-listen p {
        font-size: 1.1rem;
    } */
    .cover {
        padding-bottom: 7%;
    }
    .home-msg-container {
        padding-top: 3.5rem;
    }
    .out-now {
        margin-top: 3.2rem;
    }
    .home-links {
        padding-top: 5.5rem;
    }
    .patreon-link {
        bottom: 3%;
    }
    .album-cover {
        width: 70%;
    }
}

@media screen and (min-width: 700px)
              and (min-height: 750px) {
    h1 {
        font-size: 4.2rem;
    }
    .cover {
        padding-bottom: 12%;
    }
    .home-msg-container {
        padding-top: 4.4rem;
    }
    .out-now {
        margin-top: 3.9rem;
    }
}



@media screen and (min-width: 800px)
              and (min-height: 450px) {
    h1 {
        font-size: 4rem;
    }
    .home-msg-container {
        padding-top: 4rem;
    }
    .out-now {
        margin-top: 3.8rem;
    }
    .home-links {
        display: flex;
        justify-content: space-between;
        padding-top: 0;
        top: 40.5vh;
        width: 100%;
    }
    .home-links-pod {
        display: flex;
        justify-content: space-evenly;
        width: 25%;
    }
    .patreon-link-big {
        display: flex;
        justify-content: center;
        width: 25%;
    }
    .buy-listen {
        margin-top: 5px;
    }
    .buy-listen p {
        color: rgb(230, 230, 230);
    }
    .buy-listen {
        border-bottom: 1px solid rgb(191, 196, 161);
    }
    .album-cover {
        width: 50%;
    }
    .patreon-link {
        display: none;
    }
}

@media screen and (min-width: 800px)
              and (min-height: 1000px) {
    h1 {
        font-size: 5.6rem;
    }
    .cover {
        padding-bottom: 15%;
    }
    .home-msg-container {
        padding-top: 6.1rem;
    }
    .out-now {
        margin-top: 5.8rem;
    }
}

@media screen and (min-width: 1000px) {
    #home {
        background-size: cover, cover;
        background-position: right 50% bottom 55vh, 50% 40%;
    }
    h1 {
        font-size: 4.5rem;
    }
    .home-msg-container {
        padding-top: 4.5rem;
    }
    .out-now {
        margin-top: 4.4rem;
    }
    .cover {
        padding-bottom: 2%;
    }
    /* .buy-listen p {
        font-size: 1.2rem;
    } */
    
    /* .album-cover {
        width: 55%;
    } */
}

@media screen and (min-width: 1000px)
              and (min-height: 850px) {
                h1 {
                    font-size: 5rem;
                }
                .home-msg-container {
                    padding-top: 5rem;
                }
                .out-now {
                    margin-top: 5rem;
                }
                .cover {
                    padding-bottom: 0;
                }
              }
@media screen and (min-width: 1000px)
              and (min-height: 1100px) {
                .cover {
                    padding-bottom: 0;
                }
              }