@import url(https://fonts.googleapis.com/css2?family=Karla&family=Roboto&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.go-to-container {
    display: flex;
    justify-content: flex-end;
    position: relative;
    margin-bottom: 10px;
    z-index: 2;
}

.go-to {
    border-bottom: 1px solid rgb(255, 255, 255);
    border-top: 1px solid rgb(255, 255, 255);
    font-size: 1.1rem;
    padding: 3px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.line {
    border-bottom: 1px solid rgb(255, 255, 255);
    width: 20px;
}

.go-to p {
    /* background-color: rgba(146, 187, 79, 0.808); */
    /* color: rgb(46, 12, 70); */
    /* font-size: 1.1rem; */
    font-weight: 600;
    padding: 2px 5px;
    z-index: -1;
}

/* @media screen and (min-width: 600px) and (min-height: 450px) {
    .go-to p {
        font-size: 1rem;
    }
} */

@media screen and (min-width: 730px) and (min-height: 450px) {
    /* .go-to p {
        font-size: 1.1rem;
    } */
    .line {
        width: 35px;
    }
}

@media screen and (min-width: 1000px) {
    .go-to {
        padding: 5px;
    }
    .go-to p {
        padding: 4px 9px;
        font-size: 1.2rem;
    }
    .line {
        width: 45px;
    }
}
#home {
    background-image: url(/static/media/big_purp1.fe087c3c.png), url(/static/media/home.8c64bca0.png);
    background-size: cover, contain;
    background-position: right 50% bottom 60vh, right 50% top 40vh;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    position: relative;
}

.home-title {
    position: absolute;
    top: 0;
    left: 0;
    text-align: left;
    z-index: 2;
}

h1 {
    color: rgba(255, 255, 255, 0.2);
    font-family: 'Didot', serif;
    font-size: 3.3rem;
    font-weight: bold;
}

.home-links {
    color: rgba(255, 255, 255, 0.9);
    padding-top: 3.3rem;
    position: absolute;
    top: 6.5rem;
    text-align: left;
}

.home-msg-container {
    overflow: hidden;
    padding-top: 3.3rem;
    position: absolute;
    top: 0;
    text-align: left;
}

.home-msg {
    animation: 1s ease-out slidein;
    color: rgba(146, 170, 67, 1);
    font-size: 1rem;
    letter-spacing: 4px;
    padding-left: 6px;
    z-index: 2;
}

.out-now {
    margin-top: 2.8rem;
}

.buy-listen {
    border-bottom: 1px solid rgba(255, 255, 255, 0.9);
    margin-top: 20px;
    padding-left: 10px;
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-align: right;
    z-index: 1;
}

.patreon-link p {
    color: rgb(46, 12, 70);
    text-shadow: 0 0 30px rgba(146, 170, 67, 1), 1px 1px 20px rgba(146, 170, 67, 1), -1px -1px 20px rgba(146, 170, 67, 1), 0 10px 10px rgba(146, 170, 67, 1), 0 -10px 10px rgba(146, 170, 67, 1), 10px 0 10px rgba(146, 170, 67, 1), -10px 0 10px rgba(146, 170, 67, 1), 0 -5px 5px  rgba(146, 170, 67, 1), 0 5px 5px  rgba(146, 170, 67, 1);
}

.patreon-link-big {
    display: none;
}

.buy-listen p {
    color: rgba(255, 255, 255, 0.8);
}

.cover {
    text-align: center;
    width: 100%;
}

.album-cover {
    margin: 0 auto;
    width: 90%;
}



@keyframes slidein {
    from { 
        transform: translateX(-100%);
    }
    to { 
        transform: translateX(0);
    }
}

