    color: rgba(46, 12, 70, .7);
    cursor: pointer;
    font-size: 2.2rem;
    position: absolute;
    bottom: 45%;
    -webkit-text-fill-color: white;
    -webkit-text-stroke: 1px rgb(46, 12, 70);
    width: 45px;
}

.scrollLeft {
    left: 0;
    padding: 6px 4px 6px 0;
}

.scrollRight {
    right: 0;
    padding: 6px 0 6px 4px;
}

#menu {
    text-align: left;
    z-index: 3;
}

#fan-art-prev {
    padding-top: 60px;
}

#video {
    padding-top: 60px;
}

#tour-prev {
    padding-top: 60px;
}



/* SMILEY FOOTER */

.smiley-banner {
    background-image: url(/static/media/smiley_multi.3202f8df.png);
    background-size: 12.5%;
    height: 6.25vw;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
}



/* POP UP DISPLAYS */

.close-display-container {
    padding: 6px;
    position: absolute;
    right: 5%;
    top: 5%;
    z-index: 3;
}

.close-display-btn {
    cursor: pointer;
    height: 20px;
    width: 20px;
}

.close-display-btn span {
    background-color: white;
    border-radius: 3px;
    height: 3px;
    position: absolute;
    width: 28px;
}

.left-display {
    transform: rotate(45deg);
    transform-origin: 0 0;
    top: 0;
    left: 1px;
}

.right-display {
    transform: rotate(-45deg);
    transform-origin: 100% 100%;
    top: -1px;
    right: -1px;
}

.big-display-enter {
    opacity: 0;
}

.big-display-enter-active {
    opacity: 1;
    transition: opacity 300ms;
}

.big-display-exit {
    opacity: 1;
}

.big-display-exit-active {
    opacity: 0;
    transition: opacity 400ms;
}

.big-display {
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 3;
}

.big-display-container {
    align-items: center;
    background-image: url(/static/media/big_purp1.fe087c3c.png);
    background-size: cover;
    display: flex;
    height: 120vw;
    justify-content: center;
    width: 100%;
}

.loading {
    animation: loading-spin 2s infinite;
    color: white;
    font-family: sans-serif;
    font-size: 1.3rem;
    font-style: normal;
    transform-origin: center;
}

@keyframes loading-spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}

